.contact-container {
    background-color: #003F00;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: white;
    padding: 20px;
    height: 100px;
    margin-top: auto;
}

.social-media-icon-container {
    display: flex;
    width: 150px;
    justify-content: space-between;
}

@media (min-width:568px) {
    .contact-container {
        height: 100px;
    }


}

@media (min-width:768px) {
    .contact-container {

        height: 150px;
    }


}

@media (min-width:993px) {
    .contact-container {

        height: 200px;
    }

}

@media (min-width:1200px) {
    .contact-container {

        height: 250px;
    }


}